import React, { createContext, useContext, useEffect, useState } from "react";

const LanguageContext = createContext();

const defaultLang = typeof window !== "undefined" && localStorage.getItem("lang") || "ar";

function loadLocale(lang) {
  try {
    return require(`../locales/${lang}.json`);
  } catch {
    return require("../locales/ar.json");
  }
}

export const LanguageProvider = ({ children }) => {
  const [lang, setLang] = useState(defaultLang);
  const [messages, setMessages] = useState(loadLocale(defaultLang));

  useEffect(() => {
    localStorage.setItem("lang", lang);
    setMessages(loadLocale(lang));
  }, [lang]);

  const t = (key, vars = {}) => {
    let text = messages[key] || key;
    Object.keys(vars).forEach(k => {
      text = text.replace(`{${k}}`, vars[k]);
    });
    return text;
  };

  return (
    <LanguageContext.Provider value={{ lang, setLang, t }}>
      {children}
    </LanguageContext.Provider>
  );
};

export const useLanguage = () => useContext(LanguageContext); 